
import { defineComponent, ref } from "vue";
import MovieList from "./components/MovieList.vue";
import Error from "./components/Error.vue";
import VueTailwindPagination from "@ocrv/vue-tailwind-pagination";
import Movie from "./types/Movie";
import OrderTerm from "./types/OrderTerm";
import MovieService from "./services/MovieService.js";

export default defineComponent({
  name: "App",
  components: {
    MovieList,
    VueTailwindPagination,
    Error,
  },
  props: {},
  setup() {
    const movies = ref<Movie[]>([]);
    const order = ref<OrderTerm>("Title");
    const searchTerm = ref<string>("");
    const favourite = ref<boolean>(false);
    const loading = ref<boolean>(true);
    const page = ref<number>(1);
    const error = ref<boolean>(false);
    const errorMsg = ref<string>("");
    const statusCode = ref<number>(0);
    const totalPages = ref<number>(10);
    const favouriteMovies = ref<Movie[]>([]);
    // const perPage = ref<number>(10)

    const handleClick = (term: OrderTerm) => {
      order.value = term;
    };

    const showFavourites = () => {
      favourite.value = !favourite.value;
    };

    const searchApi = (e) => {
      handleSearch(searchTerm.value, page.value);
    };

    const handleSearch = (name: string, page: number) => {
      loading.value = true;
      favourite.value = false;
      movies.value = [];
      MovieService.searchTitle(name, page)
        .then((res) => {
          statusCode.value = res.status;
          if (statusCode.value >= 200 && statusCode.value < 300) {
            movies.value = [...res.data.data];
            page = res.data.page;
            totalPages.value = res.data.total_pages;
            searchTerm.value = "";
          } else {
            movies.value = [];
          }
          // page = res.data.page;

          error.value = res.status >= 500;
        })
        .catch((error) => {
          console.log("There was an error", error.response);
        });
      loading.value = false;
    };

    const getAll = () => {
      //initialize the values
      loading.value = true;
      favourite.value = false;
      movies.value = [];

      MovieService.getPaginated(page)
        .then((res) => {
          statusCode.value = res.status;
          if (statusCode.value >= 200 && statusCode.value < 300) {
            movies.value = [...res.data.data];
            page.value = res.data.page;
          } else {
            movies.value = [];
          }
          searchTerm.value = "";
          error.value = res.status >= 500;
        })
        .catch((error) => {
          console.log("There was an error", error.response);
        });
      loading.value = false;
    };

    const getpaginated = (event) => {
      loading.value = true;
      movies.value = [];
      MovieService.getPaginated(event)
        .then((res) => {
          statusCode.value = res.status;
          if (statusCode.value >= 200 && statusCode.value < 300) {
            movies.value = [...res.data.data];
            page.value = res.data.page;
          } else {
            movies.value = [];
          }
          searchTerm.value = "";
          error.value = res.status >= 500;
        })
        .catch((error) => {
          console.log("There was an error", error.response);
        });
      loading.value = false;
    };

    return {
      movies,
      handleClick,
      favourite,
      searchApi,
      order,
      searchTerm,
      page,
      totalPages,
      getAll,
      getpaginated,
      showFavourites,
      statusCode,
      loading,
      error,
      favouriteMovies
    };
  },
  mounted() {
    this.loading = true;
    MovieService.getMovies()
      .then((res) => {
        console.log("resonse", res);
        console.log(res.data);
        this.statusCode = res.status;
        if (this.statusCode >= 200 && this.statusCode < 300) {
          this.movies = [...res.data.data];
        } else {
          this.movies = [];
        }

        this.error = res.status >= 500;
        this.page = res.data.page;
        this.totalPages = res.data.total_pages;
      })
      .catch((error) => {
        console.log("There was an error", error.response);
      });
    this.loading = false;

    if (localStorage.favourite) {
        //get favourite movies from localstorage
        this.favouriteMovies = JSON.parse(localStorage.favourite)
      }
  },
});
